import {reactive,toRefs,ref,nextTick,defineComponent} from 'vue';
import {useRouter} from 'vue-router';
import {getRequest} from '../../request/api';
import url from '../../request/url';
import BgBox from '../../components/BgBox';
import ProductItem from '../../components/ProductItem';
export default defineComponent({
    name:'search',
    components:{
        BgBox,
        ProductItem
    },
    setup () {
        let search = ref(null);
        let state = reactive({
            name:'',
            productData:[]
        });
        let router = useRouter();
        //方法
        let onSearch = (name) => {
            getRequest(url.search,{name}).then(({code,result=[]}) => {
                if(code == 'Q001'){
                   Reflect.set(state,'productData',result);
                }else{

                }
            }).catch((err) => {
                console.log("err==>",err);
            });
        };

        let back = () => {
            router.back();
        }

        let goDetail = (pid) => {
            router.push({name: 'detail', params: {pid}});
        }
       
        nextTick(() => {
            search.value.focus();
        })
        return {
            search,
            ...toRefs(state),
            onSearch,
            back,
            goDetail
        }
    }
})