<template>
    <div class="product-item">
        <div class="product-item-img">
            <img class="auto-img" :src="data.smallImg" alt="">
        </div>
        <div class="product-item-name one-text">{{data.name}}</div>
        <div class="product-item-enname one-text">{{data.enname}}</div>
        <div class="product-item-action">
            <div class="product-item-action-price">￥{{data.price}}</div>
            <!-- <div class="fr delete" @click.stop="remove()" v-if="isRemove">
              <van-icon name="delete" />
            </div> -->

            <div class="product-item-action-delete">
                <van-icon name="delete" @click.stop="onRemove(data.pid)" />
            </div>
        </div>
    </div>
</template>

<script>
import {toRefs,defineComponent} from 'vue';
export default defineComponent({
    name:'product-item',
    props:{
        data:{
            type:Object,
            default(){
                return {}
            }
        }
    },
    setup (props,{emit}) {
        let onRemove = (pid) => {
            emit('onRemove',pid)
        }

        return {
            ...toRefs(props),
            onRemove
        }
    }
})
</script>

<style lang="less" scoped>
    .product-item{
        margin-bottom: 10px;
        .product-item-img{

        }
        .product-item-name{
            margin-top: 10px;
            font-size: 14px;
        }
        .product-item-enname{
            margin-top: 5px;
            margin-bottom: 10px;
            font-size: 12px;
            color: #999;
        }

        .product-item-action{
            display: flex;
            .product-item-action-price{
                font-size: 14px;
                color: #0C34BA;
                font-weight: bold;
            }
            .product-item-action-delete{
                margin-left: auto;
            }
        }
    }
</style>